import React from "react"
import NavigationMobile from "./NavigationMobile";
import {Link} from "gatsby"
import {useTranslation} from "react-i18next";

const Navigation = ({pageNr, isPost}) => {
    const {t, i18n} = useTranslation();

    return (
        <>
            {isPost !== true &&
                <div className="absolute css--page-number top-20 md:top-12 left-1/10" style={{zIndex: "20"}}>{pageNr}</div>
            }

            <NavigationMobile/>

            <div className="css--navbar-logo" style={{zIndex: "301"}}>
                <Link to="/">
                    <img src={"/images/brand/logo_text_White-full.svg"} style={{opacity: 0.2}} className="pt-5 md:pt-0 w-full" alt="Kalmars Logo"/>
                </Link>
            </div>
            <nav className="fixed text-center hidden md:block py-4 px-6 bg-transparent w-full" style={{zIndex: "300"}}>
                <div className="w-4/5 mx-auto">
                <div className="w-3/4 inline-flex justify-center relative">
                    <Link to="/portfolio/" className={(pageNr === 'K03PR' ? "css--navigation-active" : "") + " text-white font-bold md:mx-4 md:px-3"}>
                        {t('portfolio')}
                    </Link>
                    <Link to="/blogs/" className={(pageNr === 'MX6B1' ? "css--navigation-active" : "") + " text-white font-bold md:mx-4 md:px-3"}>
                        {t('blogs')}
                    </Link>

                    <div className="relative md:mx-4 md:px-3 css--navitem-dropdown">
                        <button className="text-white font-bold inline-flex items-center">
                            <span style={{letterSpacing: '1px'}}>{t('company')}</span>
                            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={3} stroke="currentColor" className="w-5 h-5 ml-1">
                                <path strokeLinecap="round" strokeLinejoin="round" d="M19.5 5.25l-7.5 7.5-7.5-7.5m15 6l-7.5 7.5-7.5-7.5" />
                            </svg>
                        </button>

                        <div className="absolute w-full left-0">
                            <div className="flex flex-col py-2">
                                <Link to="/about-us/" className={(pageNr === 'A0K1M' ? "css--navigation-active" : "") + " text-white font-bold py-2"}>
                                    {t('about_us')}
                                </Link>
                                <Link to="/career/" className={(pageNr === '2ET9W' ? "css--navigation-active" : "") + " text-white font-bold py-2"}>
                                    {t('career')}
                                </Link>
                            </div>
                        </div>
                    </div>

                    {/*<div className="flex justify-end">*/}
                    {/*    <button className={`${i18n.language === 'en' ? "hidden" : ""}`} onClick={changeLanguage('en')}>EN</button>*/}
                    {/*    <button className={`${i18n.language === 'lv' ? "hidden" : ""}`} onClick={changeLanguage('lv')}>LV</button>*/}
                    {/*</div>*/}
                </div>
                <div className="inline-block">
                    <Link to="/contact-us/" className={(pageNr === 'U58KL' ? "css--navigation-active" : "") + " text-white font-bold p-2"} style={{border: "2px solid #2fc7e5"}}>
                        {t('contact_us')}
                    </Link>
                </div>
                </div>
            </nav>
        </>
    )
}

export default Navigation;
